* {
  box-sizing: border-box;
}

.page {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 150%;
  color: $colorText;
  user-select: none;
  scroll-behavior: smooth;

  &__body {
    max-width: 2560px;
    margin: 0 auto;
    background-color: #e5e5e5;

    &--with-menu {
      overflow: hidden;
    }
  }


    &__menu {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      transform: translateY(-100%);
      transition: transform 0.3s;

      &:target {
        transform: translateY(0);
      }
    }

  &__section {
    margin-bottom: 148px;
  }
}
